* {
  box-sizing: border-box;
}

body {
  font-family: "Karla", sans-serif;
  margin: 0;
}

.header {
  display: flex;
  align-items: center;
  background-color: #672280;
  height: 65px;
  color: white;
  padding: 20px;
}

.header--logo {
  height: 100%;
  margin-right: 6px;
}

.header--title {
  font-size: 20px;
  margin-right: auto; /*it pushed the header--project to the right side */
}

.header--project {
  font-size: 0.75rem;
  font-weight: 500;
}

main {
  padding: 36px;
}

.form {
  display: grid;
  grid-template: 40px 40px/1fr 1fr;
  gap: 17px;
}

.form--button {
  grid-column: 1/-1;
  font-family: "Karla", sans-serif;
  background-color: #711f8d;
  color: white;
  border: none;
  cursor: pointer;
}

.form--input {
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  border: 1px solid #d5d4d8;
  text-indent: 5px;
}
.meme {
    position: relative;
}

.meme--image{
 width: 100%;
margin-top: 20px;
background-color: red;

 border-radius: 3px;
 

}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
  bottom:0;
   margin-bottom: 50px;
}

.top {
    top: 0;
    margin-top: 50px;
}